.item {
  width: 96%;
  padding: 1%;
  margin: 1%;
  color: white;
  background-color: rgb(37, 36, 36);
  border-radius: 10px;
}

.detail {
  color: black;
  background-color: white;
  margin: 1%;
  padding: 1%;
  border-radius: 5px;
}

.button {
  all: inherit;
  display: flex;
  align-items: center;
}

.button:hover {
  cursor: pointer;
}

.img {
  transform: rotate(90deg);
  animation: 1s;
}
